.imgme {
    border: 3px solid white;
    float: left;
}

.img-circle {
    border-radius: 50%;
}

#img {
    vertical-align: middle;
    border: 0;
}

#root {
    margin: 0%;
    display: block;
}

.bg1 {
    background-image: url(ukmpano.jpeg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: auto 100%;
}

#header {
    width: 100%;
    height: 400px;
    color: white;
    margin-bottom: 40px;
}

#headertext {
    margin-top: 50px;
    margin-left: 220px;
}

#headerblob {
    position: absolute;
    left: 20%;
    top: 165px;
    width: 80%;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0%;
    line-height: 1.42857143;
}

div {
    display: block;
}

html, body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    color: #444444;
}

#svgico {
    display: inline-block;
}

#htname {
    font-size: 46px;
    font-weight: 400;
}

#htdesc {
    font-size: 18px;
    font-weight: 300;
    margin-top: -6px;
}

#htem {
    font-size: 14px;
    font-weight: 300;
}

hr.soft {
    border: 0;
    height: 1px;
    background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    /* background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0)); */
    padding: 0;
    margin: 0;
  }

h2 {
    text-align: center;
    font-weight: 300;
    font-size: 50px;
    padding: 30px 0px 40px;
}

h1, .h1, h2, .h2, h3, .h3 {
    margin-top: 20px;
    margin-bottom: 10px;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
}

.t2when {
    color: rgb(132, 0, 0);
}

.t2who {
    color: rgb(0, 132, 0);
}

